import React, { useState, useEffect } from 'react'

import { Container, Row } from 'react-bootstrap'
import SectionLink from './SectionLink'

const SchoolSectionsNav = ({ userData }) => {

	const [links, setLinks] = useState(null)

	const superUserLinks = [
		{
			to: '/school/overview',
			label: ''
		},
		{
			to: '/school/classes',
			label: ''
		},
		{
			to: '/school/teachers',
			label: ''
		},
		{
			to: '/school/pupils',
			label: ''
		},
		{
			to: '/school/specialties',
			label: ''
		},
		{
			to: '/school/branches',
			label: ''
		},
		{
			to: '/school/payments',
			label: ''
		},
		{
			to: '/school/users',
			label: ''
		},
		{
			to: `/school/users/${userData.id}`,
			label: ''
		},
	]

	const teacherLinks = [
		{
			to: `/school/users/${userData.id}`,
			label: ''
		},
		{
			to: '/school/teachergroups',
			label: ''
		},
		{
			to: '/school/teacherpupils',
			label: ''
		}
	]

	useEffect(() => {
		userData.superUser ? setLinks(superUserLinks) : setLinks(teacherLinks)
	// eslint-disable-next-line
	}, [])

	return (
		<>
			<Container>
				<Row className="d-flex justify-content-center">
					{links
						? links.map(link =>
							<SectionLink
								key={link.label}
								className="py-2 px-3 section-link"
								to={link.to}
								label={link.label}
							/>)
						: null
					}
				</Row>
			</Container>
		</>
	)
}

export default SchoolSectionsNav
